.carousel-first {
  background-image: url('./img/carousel-first.jpg');
  height:400px;
}

.carousel-second {
  background-image: url('./img/carousel-second.jpg');
  height:400px;
}

.carousel-third {
    background-image: url('./img/carousel-third.jpg');
    width:100%;
    height:400px;
}

h3 {
  font-weight: 700;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
