body {
    font-family: 'Merriweather', serif;
    font-weight: 700;
    
}

.main {
    width:250px;
    height: 350px;
    background-color: #E7DDF2;
    border-radius: 30px;
    position: relative;
    margin-top: 50px;
    color:#ECF0F1;
    margin-left:50px;
    margin-bottom: 50px;
}

strong {
    font-size: 20px;
    display: inline-block;
    padding-top:10px;
}

img {
    border-radius: 30px;
    margin-top: 15px;
}

.detail {
    width:230px;
    height: 100px;
    background-color: #776FC3;
    border-radius: 30px;
    position: absolute;
    bottom:15px;
    left:9px;
}